export const addMedication = async (medicationData) => {
  const token = localStorage.getItem('token');
  const response = await axios.post(`${API_URL}/medications`, medicationData, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
  return response.data;
};


